import './App.css';
import {Container, Nav, Navbar, Row, Col} from 'react-bootstrap';
import shoes1 from './img/shoes1.jpg'
import { createContext, lazy, Suspense, useEffect, useState } from 'react';
import data from './data.js'; // js 파일은 확장자 생략해도 됨
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom'
import axios from 'axios';
import { useQuery } from 'react-query';

// import Detail from './pages/Detail'; // js 파일은 확장자 생략해도 됨
// import Cart from './pages/Cart'; // js 파일은 확장자 생략해도 됨
const Detail = lazy(() => import ('./pages/Detail'));
const Cart = lazy(() => import ('./pages/Cart'));

export let Context1 = createContext()

function App() {

  let obj = {name: 'kim'}
  localStorage.setItem('data', JSON.stringify(obj))
  let 꺼낸거 = localStorage.getItem('data')
  console.log(JSON.parse(꺼낸거).name)

  useEffect(()=>{
    let watched = JSON.parse(localStorage.getItem('watched'))
    if (watched == null) {
      localStorage.setItem('watched', JSON.stringify([]))
    }
  }, [])



  let [shoes, setShoes] = useState(data);
  let [재고] = useState([10, 11, 12])
  let [img, setImage] = useState([shoes1, process.env.PUBLIC_URL + "/shoes2.jpg", 'https://codingapple1.github.io/shop/shoes3.jpg']);
  let navigate = useNavigate();

  let result = useQuery('userData', ()=>
    axios.get("https://codingapple1.github.io/userdata.json").then((a)=>{
      return a.data
    })
  )
  console.log(result);

  return (
    <div className="App">

      <Navbar bg="light" variant="light">
        <Container>
          <Navbar.Brand>Shop-Hyuck9</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={()=>{navigate('/')}}>Home</Nav.Link>
            <Nav.Link onClick={()=>{navigate('/cart')}}>Cart</Nav.Link>
          </Nav>
          <Nav className="ms-auto">
            {/* { result.isLoading ? '로딩중' : '반가워요 ' + result.data.name } */}
            { result.isLoading && '로딩중' }
            { result.isError && '에러남' }
            { result.data && `반가워요 ${result.data.name}` }
          </Nav>
        </Container>
      </Navbar>

      <Suspense fallback={<div>로딩중</div>}>
      <Routes>
        <Route path="/" element={
          <>
          <div className="main-bg" />
          <button onClick={()=>{
            let copy = [...shoes]
            copy.sort((a, b)=> {
              if (a.title > b.title) {
                return 1;
              }
              if (a.title < b.title) {
                return -1;
              }
              return 0;
            });
            setShoes(copy);
          }}>정렬</button>
          <Container>
            <Row>
              {
                shoes.map((shoes, i)=>{
                  return (
                    <Card shoes={shoes} key={shoes.id} />
                  )
                })
              }
            </Row>
          </Container>
          <button onClick={()=>{
            axios.get('https://codingapple1.github.io/shop/data2.json')
            .then((result)=>{
              let copy = [...shoes, ...result.data]
              setShoes(copy);
            })
            .catch(()=>{
              console.log('실패함 ㅅㄱ')
            })
          }}>더보기</button>
          </>
        } />
        
        <Route path="/detail/:id" element={
          <Context1.Provider value={{ 재고, shoes }}>
            <Detail shoes={shoes} />
          </Context1.Provider>
        } />

        <Route path="/cart" element={
          <Cart />
        } />

        <Route path="/about" element={<About />} >
          <Route path="member" element={<div>멤버임</div>} />
          <Route path="location" element={<div>위치정보!</div>} />
        </Route>

        <Route path="/event" element={<EventPage />} >
          <Route path="one" element={<div>첫 주문시 양배추즙 서비스</div>} />
          <Route path="two" element={<div>생일기념 쿠폰받기</div>} />
        </Route>
        
        <Route path="*" element={<div>없는페이지에요</div>} />
      </Routes>
      </Suspense>

     </div>
  );
}


function EventPage() {
  return (
    <div>
      <h4>오늘의 이벤트</h4>
      <Outlet></Outlet>
    </div>
  )
}

function About() {
  return (
    <div>
      <h4>회사정보임</h4>
      <Outlet></Outlet>
    </div>
  )
}

function Card(props) {
  return (
    <Col md={4}>
      <Link to={"/detail/" + props.shoes.id}><img src={"https://codingapple1.github.io/shop/shoes"+ Number(props.shoes.id+1) +".jpg"} width="80%"/></Link>
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.price}</p>
    </Col>
  )
}

export default App;
